*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
.contain{
  width:100vw;
  height:100vh;
  display: grid;
  place-items:center;
  position:relative;
  background:rgb(255, 76, 76);
}
.card{
  width:30%;
  height:70%;
  box-shadow: 0px 0px 10px black;;
  background:rgba(0, 0, 0, 0.404);
  position:absolute;
 -webkit-backdrop-filter: blur(10px);
 backdrop-filter: blur(10px);
 border-radius: 1vmin;
 color:white;
 padding: 2vmin;
}

#profile{
border-radius:50%;
width:15vmin;
height:15vmin;
position:absolute;
left:37%;
top:5%;
}


#previous{
  transform:rotateZ(180deg);
  width:5vmin;
  height:5vmin;
  font-size: 3vmin;
  position:absolute;
  bottom: 5%;left:30%;
  background:rgb(0, 132, 255);
  color:white;
  border-radius:50%;
  font-weight: bold;
}
#next{
  width: 5vmin;
  height:5vmin;
  font-size: 3vmin;
  position: absolute;
  bottom:5%;
  right: 30%;
  background:rgb(0, 132, 255);
  color:white;
  border-radius:50%;
  font-weight: bold;
}
.name{
position:absolute;
top:35%;
font-size:3vmin;
left:38%;

}
.job{
  top:40%;
  left:41%;
  position:absolute;
}
.text{
  position:absolute;
  top:55%;
  left:10%;
  width:80%;
}
@media only screen and (max-width:550px){
  .card{
    width: 70%;
  }
  .name{
    font-size:5vmin;
    width: 100%;
    left: 0%;
    text-align: center;
 
  }
  .job{
    width: 100%;
    left: 0%;
    text-align: center;
  
  }
  .text{
    font-size: 4vmin;
  }
}